.type-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -ms-flex-align: center;
  align-items: center;
  padding: 10px;
  margin: 10px;
  background-color: #FEDF60;
  border-radius: 10px;
  overflow: auto;
  width: 100%;
}
@media (min-width: 992px) {
  .type-list {
    width: 50%;
    height: 80%;
    margin: 0 10px 0 0;
  }
}



