.recipe-view {
  background-color: #FEDF60;
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  font-family: helvetica;
  overflow: auto;
  padding: 10px;
  margin: 10px;
  width: 100%;
}
h1 {
  text-align: center;
  font-size: 22px;
  padding-bottom: 20px;
}
@media (min-width: 992px) {
  .recipe-view {
    width: 50%;
    height: 80%;
    margin: 0 10px 0 0;
  }
}



